      height: 20px; }

@keyframes heartbeat {
  0% {
    opacity: 0;
    stroke-width: 0px; }
  50% {
    opacity: 1;
    stroke: #7B0000;
    stroke-width: 3px; }
  100% {
    opacity: 0;
    stroke-width: .6px;
    stroke-dashoffset: 2520; } }
  body #heart-svg {
