@import "partial/reset";
@import "partial/variables";
@import "partial/helpers";

html {
    font-size: 18px;
    scroll-behavior: smooth;
}


// Basics and Layout
body {
    font-family: interstate,
        sans-serif;
    font-weight: 400;
    font-style: normal;
    color: $white;
    background-color: $black;

    .background-color {

        height: 1200vh;


        .background-svg {
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;

            svg {
                width: auto;
                height: 100%;
                fill: none;
                stroke: $red;
                stroke-miterlimit: 10;
                stroke-width: 50px;

                .blood-line {
                    transition: all 1s cubic-bezier(0, 0.0, 0, 1);
                    stroke-dashoffset: 14325.4;
                    stroke-dasharray: 14325.4;
                    stroke-linecap: round;

                }
            }

            .fluid-container {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;

                .container {
                    max-width: 80vw;
                    margin: 0 auto;
                }
            }

            .step1 {
                top: 1000px;
            }

            .step2 {
                top: 1200px;
            }

            .step3 {
                top: 1400px;
            }

            .step4 {
                top: 1600px;
            }

            .step5 {
                top: 1800px;
            }

            .step6 {
                top: 2000px;
            }

            .step7 {
                top: 2200px;
            }

            .step8 {
                top: 2400px;
            }

            .step9 {
                top: 2600px;
            }

            .step10 {
                top: 2800px;
            }
        }

    }

    .heartbeat-line-svg {
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;

        .heartbeat-path {
            stroke: $red;
            stroke-width: 2px;
            stroke-dasharray: 1260;
            stroke-dashoffset: 1260;
            fill: none;
            animation: heartbeat 1.6s linear infinite;
            height: 20px;
        }
    }

    @keyframes heartbeat {
        0% {
            opacity: 0;
            stroke-width: 0px;
        }

        50% {
            opacity: 1;
            stroke: $red;
            stroke-width: 3px;
        }

        100% {
            opacity: 0;
            stroke-width: .6px;
            stroke-dashoffset: 2520;
        }

    }

    #heart-svg {
        position: fixed;
        top: calc(50vh - 100px);
        left: 0;
        height: 200px;
        width: 200px;

        svg {
            stroke: $black;
            stroke-width: 5px;
        }


        .heartPfadRechts {
            fill: $red;

        }

        .heartPfadLinksOben {
            fill: $red;
        }

        .heartPfadLinksUnten {
            fill: $red;
        }

    }

}

section {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}



// Font

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 700;
}

h1 {
    font-size: 5rem;
    // text-transform: uppercase;
}